import Typography from "@mui/material/Typography";
import chara from "./chara";
import Grid from "@material-ui/core/Grid";

function charas(index) {
  const items = [];
  const keys = Object.keys(chara[index]);
  const values = Object.values(chara[index]);
  for (let i = 0; i < values.length; i++) {
    items.push(
      <li>
        {keys[i]}:{values[i]}
      </li>
    );
  }
  return <ul>{items}</ul>;
}

